.Page{
  background-color: rgb(214, 191, 157);
  color:#000000;
}
a{
  text-decoration:none;
  color:rgb(175,202,144);
}
a:hover{
  color:yellow;
}
a:active{
  color:white;
}
.TopPage{
  margin:0 1%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}
.logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Header{
  display:flex;
  text-decoration:none;
  background-color:rgb(51,60,26)!important;
  height:10vmin;
  width:100%;
  align-items: center;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
}
.HeaderTop{
  padding:10px;
}
.HeaderMenus{
  display:flex;
  text-decoration:none;
}
.berger{
  display:none;
}
.HeaderMenu{
  margin:5px;
  padding:2px;
}

.TopPageGreeting{
  display:flex;

}
.TopPageGreetingImage{
  width:50vmin;
}
.TopPageGreetingText{
  margin:3%;
}

@media (max-width: 1150px){
  .HeaderMenus{
    display:none;
  }
  .berger{
    display:inline-block;
  }
}

.mainContent{
  background-color: #ffffff;
  margin:1vmin;
  padding:1vmin;
}
